import Radium from 'radium';
import React from 'react';
import { Link, NavLink } from 'react-router-dom';

const NavigationItemsStyles = {
    NavigationItems: {
        margin: '0',
        padding: '0',
        listStyle: 'none',
        display: 'flex',
        flexFlow: 'column',
        alignItems: 'center',
        height: '100%',
        '@media (min-width: 500px)': {
            flexFlow: 'row',
        }
    },
    NavigationItem: {
        margin: '10px 0',
        padding: '10px',
        boxSizing: 'border-box',
        display: 'block',
        width: '100%',
        '@media (min-width: 500px)': {
            margin: '0',
            display: 'flex',
            height: '100%',
            width: 'auto',
            alignItems: 'center',
        }
    },
    a: {
        color: 'white',
        textDecoration: 'none',
        width: '100%',
        boxSizing: 'border-box',
        display: 'block',
        ':hover': {
            backgroundColor: '#8F5C2C',
            borderBottom: '4px solid #40A4C8',
            color: 'white'
        },
        '@media (min-width: 500px)': {
            color: 'white',
            height: '100%',
            padding: '16px 10px',
            borderBottom: '4px solid transparent',
        }
    }
}
const NavigationItems = (props) => {
    return (
        <ul style={NavigationItemsStyles.NavigationItems}>
            <li style={NavigationItemsStyles.NavigationItem}>
                <NavLink
                    style={NavigationItemsStyles.a}
                    to='/'
                    exact
                    activeStyle={{ color: 'green' }}>Burger Builder</NavLink>
            </li>
            <li style={NavigationItemsStyles.NavigationItem}>
                <NavLink
                    style={NavigationItemsStyles.a}
                    to="/checkout"
                    activeStyle={{ color: 'green' }} >Checkout</NavLink>
            </li>
        </ul>
    );
}

export default Radium(NavigationItems);