import React from 'react';
import Button from '../../../components/ui/button/Button';

const styles = {
    CondactData: {
        margin: '20px auto',
        width: '80%',
        textAlign: 'center',
        boxShadow: '0 2px 3px #ccc',
        border: '1px solid #eee',
        padding: '10px',
        boxSizing: 'border-box'
    },
    Input: {
        display: 'block'
    }
}
class CondactData extends React.Component {

    state = {
        name: '',
        email: '',
        address: {
            street: '',
            postalCode: ''
        }
    }

    orderHandler = (event) => {
        event.preventDefault();
        // console.log(this.props.ingrediants)
        this.props.history.push('/');
    }

    render() {
        return (
            <div style={styles.CondactData}>
                <h1>Enter Your Condact Data</h1>
                <form>
                    <input style={styles.Input} type='input' name='name' placeholder='name' />
                    <input style={styles.Input} type='email' name='email' placeholder='email' />
                    <input style={styles.Input} type='input' name='street' placeholder='street' />
                    <input style={styles.Input} type='input' name='postalCode' placeholder='postal code' />
                    <Button type='btn-success' clicked={this.orderHandler}>ORDER</Button>
                </form>
            </div>
        )
    }
}

export default CondactData;