import Radium from 'radium';
import React from 'react';

const DrawerToggleStyles = {
    DrawerToggle: {
        width: '40px',
        height: '100%',
        display: 'flex',
        flexFlow: 'column',
        justifyContent: 'space-around',
        alignItems: 'center',
        padding: '10px 0',
        boxSizing: 'border-box',
        cursor: 'pointer',
        '@media (min-width: 500px)': {
            display: 'none',
        }
    },
    div: {
        width: '90%',
        height: '3px',
        backgroundColor: 'white',
        '@media (min-width: 500px)': {
            display: 'none',
        }
    }
}

class DrawerToggle extends React.Component {
    render() {
        return (
            <div style={DrawerToggleStyles.DrawerToggle} onClick={this.props.clicked}>
                <div style={DrawerToggleStyles.div}></div>
                <div style={DrawerToggleStyles.div}></div>
                <div style={DrawerToggleStyles.div}></div>
            </div>
        )
    }
}

export default Radium(DrawerToggle);