import React from 'react';

const BackdropStyles = {
    Backdrop: {
        width: '100%',
        height: '100%',
        position: 'fixed',
        zIndex: '100',
        left: '0',
        top: '0',
        backgroundColor: 'rgba(0, 0, 0, 0.5)'
    }
}
const Backdrop = (props) => {
    return props.show ? <div style={BackdropStyles.Backdrop} onClick={props.clicked}></div> : null;
}

export default Backdrop;