import React from 'react';
import Aux from '../../../hoc/Auxillary';
import Button from '../../ui/button/Button';

const OrderSummary = (props) => {
    const ingrediantSummary = Object.keys(props.ingrediants).map(igKey => {
        return <li key={igKey}><span style={{ textTransform: 'capitalize' }}>{igKey}</span>: {props.ingrediants[igKey]}</li>
    });
    return (
        <Aux>
            <h3>Your Order</h3>
            <p>A delicious burger with following Ingrediants:</p>
            <ul>
                {ingrediantSummary}
            </ul>
            <p>Total Price:<strong>{props.price.toFixed(2)}</strong></p>
            <p>Continue to Checkout?</p>
            <Button clicked={props.cancelPurchase}>CANCEL</Button>
            <Button clicked={props.continuePurchase}>CONTINUE</Button>
        </Aux>
    );
}

export default OrderSummary;