import React from 'react';
import Burger from '../burger/Burger';
import Button from '../ui/button/Button';

const styles = {
    CheckoutSummary: {
        width: '80%',
        margin: 'auto',
        textAlign: 'center'
    }
}
const CheckoutSummary = (props) => {
    return (
        <div style={styles.CheckoutSummary}>
            <h1>We hope it tastes well!!!</h1>
            <div style={{ width: '300px', height: '300px', margin: 'auto' }}>
                <Burger ingrediants={props.ingrediants} />
            </div>
            <div style={{ marginTop: '60px' }}>
                <Button type='btn btn-danger' clicked={props.cancelCheckout}>CANCEL</Button>
                <Button type='btn btn-primary' clicked={props.continueCheckout}>Continue</Button>
            </div>

        </div>
    );
}

export default CheckoutSummary;