import React from 'react';
import Burger from '../../components/burger/Burger';
import Aux from '../../hoc/Auxillary';
import { StyleRoot } from 'radium';
import BuildControls from '../../components/burger/buildControls/BuildControls';
import Modal from '../../components/ui/modal/Modal';
import OrderSummary from '../../components/burger/orderSummary/OrderSummary';
import * as actionTypes from '../../store/Actions';
import { connect } from 'react-redux';
import AxiosInstance from '../../AxiosInstance';


class BurgerBuilder extends React.Component {

    state = {
        totalPrice: 4,
        purchasable: false,
        purchasing: false
    }

    componentDidMount() {
        AxiosInstance.get('/ingrediants.json').then(res => {
            // console.log('res', res)
            this.props.getIngrediantFromServer(res.data);
        }).catch(err => {

        })
    }

    purchaseHandler = () => {
        this.setState({purchasing: true})
    }

    updatePurchaseState(ingrediants) {
        // const ingrediants = { ...this.state.ingrediants };
        const sum = Object.keys(ingrediants).map(igKey => {
            return ingrediants[igKey]
        }).reduce((sum, el) => {
            return sum + el
        }, 0)
        
        return sum > 0;
        
    }


    purchaseCancelHandler = () => {
        let tmp_purchasing = this.state.purchasing;
        tmp_purchasing = false;
        this.setState({
            purchasing: tmp_purchasing
        })
    }

    purchaseContinueHandler = () => {
        
        this.props.history.push('/checkout');
    }
    
    render() {
        let disabledInfo = {...this.props.ings};
        for (let key in disabledInfo) {
            disabledInfo[key] = disabledInfo[key] <= 0;
        }
        return (
            <Aux>
                <Modal show={this.state.purchasing} modalClosed={this.purchaseCancelHandler}>
                    <OrderSummary
                        ingrediants={this.props.ings}
                        cancelPurchase={this.purchaseCancelHandler}
                        continuePurchase={this.purchaseContinueHandler}
                        price={this.props.totalPrice}
                    />
                </Modal>

                <Burger ingrediants={this.props.ings} />


                <BuildControls
                    addIngrediant={this.props.onIngrediantAdded}
                    removeIngrediant={this.props.onIngrediantRemoved}
                    disabled={disabledInfo}
                    totalPrice={this.props.totalPrice}
                    purchasable={this.updatePurchaseState(this.props.ings)}
                    ordered={this.purchaseHandler}
                />
            </Aux>
        );
    }
}

const mapStateToProps = state => {
    return {
        ings: state.ingrediants,
        totalPrice: state.totalPrice
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getIngrediantFromServer: (ings) => dispatch({type: actionTypes.GET_INGREDIANT_FROM_SERVER, ings: ings}),
        onIngrediantAdded: (ingName) => dispatch({ type: actionTypes.ADD_INGREDIANT, ingrediantName: ingName }),
        onIngrediantRemoved: (ingName) => dispatch({ type: actionTypes.REMOVE_INGREDIANT, ingrediantName: ingName })
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(BurgerBuilder);