import React from 'react';
import Logo from '../../logo/Logo';
import NavigationItems from '../navigationItems/NavigationItems';
import Radium from 'radium';
import Aux from '../../../hoc/Auxillary';
import Backdrop from '../../ui/backdrop/Backdrop';

const SideDrawerStyles = {
    SideDrawer: {
        position: 'fixed',
        width: '280px',
        maxWidth: '70%',
        height: '100%',
        left: '0',
        top: '0',
        zIndex: '200',
        backgroundColor: 'white',
        padding: '32px 16px',
        boxSizing: 'border-box',
        transition: 'transform 0.3s ease-out',
        '@media (min-width: 500px)': {
            display: 'none'
        }
    },
    Open: {
        transform: 'translateX(0)'
    },
    Close: {
        transform: 'translate(-100%)'
    }
}
class SideDrawer extends React.Component {
    render() {
        let attachedStyle = {...SideDrawerStyles.SideDrawer, ...SideDrawerStyles.Close}
        if (this.props.open) {
            attachedStyle = {...SideDrawerStyles.SideDrawer, ...SideDrawerStyles.Open}
        }
        return (
            <Aux>
                <Backdrop show={this.props.open} clicked={this.props.closed} />
                <div style={attachedStyle}>
                <div style={{ height: '11%', marginBottom: '32px' }}>
                    <Logo />
                </div>

                <nav>
                    <NavigationItems />
                </nav>
            </div>
            </Aux>
            
        );
    }
}
export default Radium(SideDrawer);