import * as actionTypes from '../store/Actions';

const initialState = {
    ingrediants: {
        salad: 0,
        bacon: 0,
        cheese: 0,
        meat: 0
    },
    totalPrice: 4,
}
const INGREDIANT_PRICES = {
    salad: 0.5,
    bacon: 0.6,
    cheese: 0.7,
    meat: 0.8
}

const reducer = (state = initialState, action) => {

    switch(action.type) {
        case actionTypes.ADD_INGREDIANT:
            return {
                ...state,
                ingrediants: {
                    ...state.ingrediants,
                    [action.ingrediantName]: state.ingrediants[action.ingrediantName] + 1
                },
                totalPrice: state.totalPrice + INGREDIANT_PRICES[action.ingrediantName]
            };
        case actionTypes.REMOVE_INGREDIANT:
            return {
                ...state,
                ingrediants: {
                    ...state.ingrediants,
                    [action.ingrediantName]: state.ingrediants[action.ingrediantName] - 1
                },
                totalPrice: state.totalPrice - INGREDIANT_PRICES[action.ingrediantName]
            };

        case actionTypes.GET_INGREDIANT_FROM_SERVER:
            return {
                ...state,
                ingrediants: action.ings,
                totalPrice: 4
            }
        default:
            return state;
    }
};

export default reducer;