import React from 'react';

const BuildControlStyles = {
    BuildControl: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        margin: '5px 0',

    },

    Label: {
        padding: '10px',
        fontWeight: 'bold',
        width: '80px',
    },
    Less: {
        backgroundColor: '#D39952',
        color: 'white',
        ':active': {
            backgroundColor: '#DAA972',
            color: 'white',
        },
        ':hover': {
            backgroundColor: '#DAA972',
            color: 'white',
        }
    },
    More: {
        backgroundColor: '#8F5E1E',
        color: 'white',
        ':active': {
            backgroundColor: '#99703F',
            color: 'white',
        },
        ':hover': {
            backgroundColor: '#99703F',
            color: 'white',
        }
    },

    BuildControlButton: {
        display: 'block',
        font: 'inherit',
        padding: '5px',
        margin: '0 5px',
        width: '80px',
        border: '1px solid #AA6817',
        cursor: 'pointer',
        outline: 'none',
        ':disabled': {
            backgroundColor: '#AC9980',
            border: '1px solid #7E7365',
            color: '#ccc',
            cursor: 'default',
        },
        ':hover:disabled': {
            backgroundColor: 'red',
            color: '#ccc',
            cursor: 'not-allowed',
        }
    }

}

const lessBtnStyles = Object.assign({}, BuildControlStyles.BuildControlButton, BuildControlStyles.Less)
const moreBtnStyles = Object.assign({}, BuildControlStyles.BuildControlButton, BuildControlStyles.More)

const BuildControl = (props) => {
    return (
        <div style={BuildControlStyles.BuildControl}>
            <div style={BuildControlStyles.Label}>{props.label}</div>
            <button key={'less' + props.label} style={BuildControlStyles.BuildControlButton} onClick={props.removed} disabled={props.disabled}>Less</button>
            <button key={'more' + props.label} style={moreBtnStyles} onClick={props.added}>More</button>
        </div>
    );
}


export default BuildControl;