import Radium from 'radium';
import React from 'react';
import classes from '../../../App.css';

const loaderStyles = {
    loader: {
        color: '#341133',
        fontSize: '11px',
        textIndent: '-99999em',
        margin: '55px auto',
        position: 'relative',
        width: '10em',
        height: '10em',
        boxShadow: 'inset 0 0 0 1em',
        WebkitTransform: 'translateZ(0)',
        MsTransform: 'translateZ(0)',
        transform: 'translateZ(0)',
        borderRadius: '50%',
        ':before': {
            width: '5.2em',
            height: '10.2em',
            background: '#eef1f1',
            borderRadius: '10.2em 0 0 10.2em',
            top: '-0.1em',
            left: '-0.1em',
            transformOrigin: '5.1em 5.1em',
            animation: 'load2 2s infinite ease 1.5s',
            position: 'absolute',
            content: '',
            WebkitTransformOrigin: '5.1em 5.1em',
            WebkitAnimation: 'load2 2s infinite ease 1.5s',
        },
        ':after': {
            width: '5.2em',
            height: '10.2em',
            background: '#eef1f1',
            borderRadius: '0 10.2em 10.2em 0',
            top: '-0.1em',
            left: '4.9em',
            transformOrigin: '0.1em 5.1em',
            animation: 'load2 2s infinite ease',
            position: 'absolute',
            content: '',
            WebkitTransformOrigin: '0.1em 5.1em',
            WebkitAnimation: 'load2 2s infinite ease',
        },
        '@WebkitKeyframes load2': {
            '0%': {
                WebkitTransform: 'rotate(0deg)',
                transform: 'rotate(0deg)',
            }
        },
        '@keyframes load2': {
            '0%': {
                WebkitTransform: 'rotate(0deg)',
                transform: 'rotate(0deg)',
            }
        }
    }
}
const Spinner = () => (
    <div style={loaderStyles.loader}>Loading...</div>
);

export default Radium(Spinner);